<template>
  <div>
    <div class="header_submit">
      <van-nav-bar title="结算中" left-arrow @click-left="onClickLeft">
        <van-icon name="ellipsis" slot="right" />
      </van-nav-bar>
    </div>
    <div class="submit_main">
      <div class="submit_loading" v-show="!isflag">
        <van-loading size="24px">加载中...</van-loading>
      </div>
      <div class="submit_success" v-show="isflag">
        <van-button plain hairline type="primary">提交成功</van-button>
      </div>
    </div>
    <div class="submit_footer">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <!-- <van-cell v-for="item in list" :key="item" :title="item" /> -->
        <div v-for="item in num" :key="item">
          <van-row>
            <van-col span="12">
              <img src="../assets/libs/img/success_img.webp" />
            </van-col>
            <van-col span="12">
              <img src="../assets/libs/img/success_img.webp" />
            </van-col>
          </van-row>
        </div>
      </van-list>
    </div>
  </div>
</template>

<style lang="scss">
.header_submit {
  .van-icon {
    color: black;
    font: 20px/1 "vant-icon";
  }
  .van-nav-bar .van-icon {
    color: black;
  }
}

.submit_main {
  .submit_loading {
    text-align: center;
    margin: 10px 0;
  }
  .submit_success {
    text-align: center;
    margin: 10px 0;
  }
}
.submit_footer {
  img {
    width: 100%;
  }
}
</style>

<script>
export default {
  data() {
    return {
      list: [],
      num: 0,
      loading: false,
      finished: false,
      isflag: false
    };
  },

  methods: {
    onLoad() {
      // 异步更新数据
      setTimeout(() => {
        this.num++;
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.num >= 7) {
          this.finished = true;
        }
      }, 500);
    },
    onClickLeft() {
      this.$router.push("/wd");
    }
  },
  created() {
    setTimeout(() => {
      this.isflag = true;
    }, 2000);
  }
};
</script>